.header__container {
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

.burger__open, .burger__menu {
  display: none;
}

@media (max-width: 900px) {
  .burger__menu {
    width: 100%;
    height: 100%;
    z-index: 20;
    background-color: #fff;
    transition: all .5s;
    display: block;
    position: fixed;
    top: 140px;
    left: 0;
    overflow: auto;
    transform: translateX(-100%);
  }

  .burger__menu.active {
    transform: translateX(0%);
  }

  .burger__open {
    width: 30px;
    height: 20px;
    z-index: 5;
    cursor: pointer;
    display: block;
    position: absolute;
    top: 20px;
    left: 20px;
  }

  .burger__open:before, .burger__open:after {
    content: "";
    width: 100%;
    height: 2px;
    transition: all 1s;
    position: absolute;
    left: 0;
  }

  .burger__open:before {
    background-color: #fff;
    top: 0;
  }

  .burger__open:after {
    background-color: #fff;
    bottom: 1px;
  }

  .burger__open span {
    width: 100%;
    height: 2px;
    background-color: #fff;
    position: absolute;
    top: 9px;
    left: 0;
  }

  .burger__open.active:before {
    background-color: #fff;
    top: 9px;
    transform: rotate(45deg);
  }

  .burger__open.active:after {
    background-color: #fff;
    bottom: 9px;
    transform: rotate(-45deg);
  }

  .burger__open.active span {
    transform: scale(0);
  }
}

.burger__nav {
  justify-content: start;
  padding: 30px;
  display: flex;
}

.burger__list {
  transition: all .3s;
}

.burger__list:not(:last-child) {
  margin-bottom: 25px;
}

.burger__link {
  text-align: left;
  font-family: Play;
  font-size: 20px;
  font-weight: 700;
  line-height: 90%;
  transition: all .3s;
  position: relative;
}

.burger__link:after {
  content: "";
  width: 100%;
  height: 2px;
  transform-origin: 100%;
  background-color: #068292;
  transition: transform .5s;
  position: absolute;
  top: 100%;
  left: 0;
  transform: scaleX(0);
}

.burger__link:hover {
  color: #1ab9ce;
}

.burger__link:hover:after {
  transform-origin: 0;
  transform: scaleX(1);
}

